<template>
<div>
   <section class="head">
      <div> <a @click="goBack" class="iconfont back"> &#xe614; </a> </div>
      <h1>Browse record</h1>
      <div class="chartWrap">
        <a class="iconfont cart"> &#xe669; </a></div>
    </section>
    <section class="downListWrap">
      <div class="commonPro">
        <ul class="clearfix" :class="{'turnLine':showStyle==1,'Prolist':showStyle==2}" >
          <li  v-for="(item,index) in list" :key="index" @click="$router.push({path:'proDetail',query:{spuCode:item.spuCode}})">
                <div class="tips_cutOff" v-if="item.percentage"> {{item.percentage}} OFF </div>
                <div class="tips_sale" v-if="item.sale"> </div>
                <div class="imgWrap">
                  <img class="img" :src="item.spuMainImage">
                </div>
                <div class="rightMain">
                  <div class="name"> {{item.name}} </div>
                  <div class="downWrap">
                    <div class="bottomPrice">
                      <p class="red"> {{item.priceRange}} AED  </p>
                      <p class="gray"> {{item.priceRangeLine}} AED </p>
                      <p v-if="item.isTypeFour==1"> Free Shipping </p>
                    </div>
                    <div class="downShare">
                      <p class="others"> {{item.sumSku}} Other Items </p>
                      <div :class="{'active':item.cart}"> <span class="cart iconfont"> &#xe669; </span> </div>
                      <div :class="{'active':item.share}"> <span class="share iconfont"> &#xe650; </span> </div>
                       <div :class="{'active':item.share}"> <span class="share iconfont"> &#xe609; </span> </div>                     
                    </div>
                  </div>
                </div>
          </li>
        </ul>
      </div>
    </section>

 

  </div>
</template>

<script>
import {getUserBrowseProduct} from '@/api/user'
export default {
  name: 'browseRecord',
  data () {
    return {
      list:{},
      sortVisible:false,
      searchValue:"",
      sortValue:2,
      sortType:[
        { value:1,
          label:'Price:Low to High'
        },
        {value:2,
          label:'Price:High to Low'
        },
        {value:3,
          label:'Best Sale'
        },
        {value:4,
          label:'High Rated'
        }
      ],
      showStyle:1,
      show:false,
      toggle:{
        "Categories":1,
        "Brand":0
      },
      priceRange:["",""],
      radioValue:1,
      radioList:[
        {
          "label":"Sub-Categories1 (99)",
          "value":1
        },
        {
          "label":"Sub-Categories2",
          "value":2
        },
        {
          "label":"Sub-Categories3",
          "value":3
        }
      ],
      radioValue2:1,
      radioList2:[
        {
          "label":"Sub-Brand1 (99)",
          "value":1
        },
        {
          "label":"Sub-Brand2",
          "value":2
        },
        {
          "label":"Sub-Brand3",
          "value":3
        },
        {
          "label":"Sub-Brand4",
          "value":4
        }
      ],
      proDataBest:[
        {
          "cutOff":"50%",
          "love":1,
          "img":"static/images/bestPro1.png",
          "name":"Name of the stor Name of the store and  shopeone Name of the store and  shopeone  e and  shopeone Name of the store and  shopeone ",
          "price":341.22,
          'hisPrice':682.44,
          "free":1,
          "cart":1,
          "share":0,
          "sale":1,
          "oterNum":2
        },
        {
          "cutOff":"50%",
          "love":0,
          "img":"static/images/bestPro2.png",
          "name":"Name of the store and  shopeone Name of the store and  shopeone ",
          "price":341.22,
          'hisPrice':682.44,
          "free":0,
          "cart":0,
          "share":0,
          "sale":0,
          "oterNum":2
        },
      ]
    }
  },
  computed: {
  },
  mounted() {
    this.init();
  },
  methods: {
    init(){
       getUserBrowseProduct({pageNo:1,pageSize:10}).then(res=>{
          this.list=res.data.result;
       });
    },
    changeSort(val){
      this.sortValue=val;
      this.sortVisible=false;
    },
    goBack(){
      this.$router.back(-1)
    }


  }
}
</script>

<style lang="scss" scoped>
  @import "../common/css/search.scss";
  .downShare{
    position: absolute !important;
    width: 90px;
    right: 20px;
    bottom: 15px;
  }
</style>



